@import "../settings";
@import "../mixins";
@import "../fonts/roboto";
@import "../fonts/steelfish";
$roboto-regular: RobotoSlabRegular, georgia, serif;
$roboto-bold: RobotoSlabBold, georgia, serif;
$roboto-extrabold: RobotoSlabExtrabold, georgia, serif;
.info-fonts {
.extrabold, .extra-bold {
@include extrabold();
}

.roboto {
font-family: RobotoSlabRegular, georgia, serif;
	i, em {
	font-style: italic;
	font-weight: inherit;
	font-size: inherit;
	}
	b, strong {
	font-style: inherit;
	font-weight: 700;
	font-size: inherit;
	font-family: RobotoSlabBold, georgia, serif;
	}
}


.steelfish, .condensed {
font-family: steelfish, "Arial Narrow", Arial, san-serif !important;
	i, em {
	font-style: italic;
	font-weight: inherit;
	font-size: inherit;
	}
	b, strong {
	font-style: inherit;
	font-weight: 700 !important;
	font-size: inherit;
	font-family: steelfish-bold,  "Arial Narrow", Arial, sans-serif !important;
	
	}
	&.extrabold {
	font-weight: 800 !important;
	font-family: steelfish-extrabold, "Arial Black", Arial, sans-serif !important;
	}
}

.smaller {
  font-size: 85%; }

.largest {
  font-size: 300%}

.larger {
  font-size: 200%}

.large {
  font-size: 150%;
  line-height: inherit !important; }

.condensed {
  font-weight: 400;
  font-family: steelfish, 'Arial Narrow', sans-serif !important; 

&.huge {
  font-size: 3rem;
  display: block;
  line-height: 0;
  margin-bottom: 0;
  padding-top: 0;
  margin-top: 0; }
  
  &.medium {
  font-size: 2rem; }

&.large.condensed {
  font-size: 3rem; }
  
  &.large {
  font-size: 3.5rem; }
  
  &.huge {
  font-size: 4rem; }
  }

 .crunch {
line-height: .75 !important;
}
  } //end infofonts
  
  .dashed {
   border-style: dashed !important;
   }
   
  
  
  aside .large {
  font-size: 125%;
  }