@import "../settings";

@mixin light() {
font-family: $light-font-family-sans-serif;
font-weight: $font-weight-light;
font-style: inherit;
}
 @mixin regular() {
font-family: $font-family-sans-serif;
font-weight: $font-weight-regular;
font-style: inherit;
}

@mixin bold() {
font-family: $bold-font-family-sans-serif;
font-weight: $font-weight-bold;
font-style: inherit;
}

@mixin semibold() {
font-family: $semibold-font-family-sans-serif;
font-weight: $font-weight-semibold;
font-style: inherit;
}

@mixin extrabold() {
font-family: $extrabold-font-family-sans-serif;
font-weight: $font-weight-extrabold;
font-style: inherit;
}

@mixin italic() {
font-family: OpenSansItalic, Helvetica, Arial, sans-serif;
font-weight: inherit !important;
font-style: italic;
}

@mixin bold-italic() {
font-family: OpenSansBoldItalic, Helvetica, Arial, sans-serif;
font-weight: 700 !important;
font-style: italic;
}


@mixin smaller() {
font-size: 75%;
}




@mixin center-content-in-div () {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
@mixin center-content-vertical-in-div () {
	position: absolute;
	top: 50%;
	left: 0;
	transform: translate(0, -50%);
}
//  We use this to change page color theme.
@mixin page-theme($theme-color) {
	.theme-header {
		font-size: $page-theme-header-size;
		.theme {
			width: $page-theme-header-icon-size;
			//padding-right: 0;
			margin-right: -1rem;
			margin-left: -.8rem;
			margin-bottom: -1rem;
			height: 4rem;
			color: $theme-color;
			fill: $theme-color;
			stroke: $theme-color;

			} //end theme icon style
		span a {
				display: block;
				font-size: 43% !important;
				color: $theme-color;
				margin: -.5rem 0 0 4.3rem;
				text-transform: none;
		}
		@media screen and (max-width: 25em) {
			.theme {
				margin-right: -.5rem;
				font-size: 2rem;
				} //end theme icon style
			span a {
				margin-left: 3.2rem;
				margin-top: -.2rem;
				font-size: 43% !important;
			}
		}
		a {
		@include light();
		text-transform: capitalize;
		color: $theme-color;
			.st0 {
				fill: $theme-color;
				}
			.str, .str2 {
			stroke: $theme-color;

			}

		&:hover {
			color: lighten($theme-color, 10%);
			border-bottom: 1px dotted lighten($theme-color, 10%);
			 .st0 {
				fill: lighten($theme-color, 10%);
				stroke: lighten($theme-color, 10%);
				}
				.str, .str2 {
				stroke: lighten($theme-color, 10%);
				}

		} //end hover

	} //end a
} //end theme -header


.subcat-icon {
		.st0 {
		fill: $theme-color;

		}
		.str, .str2 {
		stroke: $theme-color;
		}

}  //end a subcat-icon

a:hover {
	h2 {
	color: $link;
	}
.subcat-icon {
	 .st0 {
		fill: lighten($theme-color, 10%);
		}
	.str, .str2 {
		stroke: lighten($theme-color, 10%);
		}
} //end hover subcat-icon


} //end a hover



} //end mixin






@mixin close-btn ($btn-color: $gray) {
  color: $btn-color;
  font-size: rem-calc(50);
  position: absolute;
  right: rem-calc(10);
  top: rem-calc(20);
  line-height: rem-calc(20);
  outline: none;
}

