$font-path: "/fonts/";
@font-face {
    font-family: 'steelfish-bold';
    src: url($font-path + 'steelfish/steelfish_bd-webfont.eot');
    src: url($font-path + 'steelfish/steelfish_bd-webfont.eot?#iefix') format('embedded-opentype'),
         url($font-path + 'steelfish/steelfish_bd-webfont.woff2') format('woff2'),
         url($font-path + 'steelfish/steelfish_bd-webfont.woff') format('woff'),
         url($font-path + 'steelfish/steelfish_bd-webfont.ttf') format('truetype'),
         url($font-path + 'steelfish/steelfish_bd-webfont.svg#steelfish_rgbold') format('svg');
    font-weight: 700;
    font-style: normal;

}

 @font-face {
    font-family: 'steelfish-extrabold';
    src: url($font-path + 'steelfish/steelfish_eb-webfont.eot');
    src: url($font-path + 'steelfish/steelfish_eb-webfont.eot?#iefix') format('embedded-opentype'),
         url($font-path + 'steelfish/steelfish_eb-webfont.woff2') format('woff2'),
         url($font-path + 'steelfish/steelfish_eb-webfont.woff') format('woff'),
         url($font-path + 'steelfish/steelfish_eb-webfont.ttf') format('truetype'),
         url($font-path + 'steelfish/steelfish_eb-webfont.svg#steelfishextrabold') format('svg');
    font-weight: 900;
    font-style: normal;

}

@font-face {
    font-family: 'steelfish';
    src: url($font-path + 'steelfish/steelfish_rg-webfont.eot');
    src: url($font-path + 'steelfish/steelfish_rg-webfont.eot?#iefix') format('embedded-opentype'),
         url($font-path + 'steelfish/steelfish_rg-webfont.woff2') format('woff2'),
         url($font-path + 'steelfish/steelfish_rg-webfont.woff') format('woff'),
         url($font-path + 'steelfish/steelfish_rg-webfont.ttf') format('truetype'),
         url($font-path + 'steelfish/steelfish_rg-webfont.svg#steelfish_rgregular') format('svg');
    font-weight: 400;
    font-style: normal;

}
