@font-face {
    font-family: 'RobotoSlabRegular';
    src: 
    url('/fonts/roboto/RobotoSlab-Regular-webfont.woff2') format('woff2'),
    url('/fonts/roboto/RobotoSlab-Regular-webfont.woff') format('woff'),
    url('/fonts/roboto/RobotoSlab-Regular-webfont.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'RobotoSlabBold';
    src:
         url('/fonts/roboto/RobotoSlab-Bold-webfont.woff2') format('woff2'), url('/fonts/roboto/RobotoSlab-Bold-webfont.woff') format('woff'),
         url('/fonts/roboto/RobotoSlab-Bold-webfont.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'RobotoSlabExtrabold';
    src:
          url('/fonts/roboto/RobotoSlab-ExtraBold.woff2') format('woff2'),
        url('/fonts/roboto/RobotoSlab-ExtraBold.woff') format('woff'),
         url('/fonts/roboto/RobotoSlab-ExtraBold.ttf') format('truetype');
    font-weight: 800;
    font-style: normal;
}


@font-face {
    font-family: 'RobotoSlabBlack';
    src:
          url('/fonts/roboto/RobotoSlab-Black.woff2') format('woff2'),
        url('/fonts/roboto/RobotoSlab-Black.woff') format('woff'),
         url('/fonts/roboto/RobotoSlab-Black.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
}






